.greetingForm {
  display: flex;
  flex-direction: column;
  align-self: center;
  row-gap: 10px;
}

.inputAndButtonContainer {
  display: flex;
}

.error {
  margin-top: 5px;
  font-size: 12px;
  color: #c00;
}

.input {
  width: 300px;
  height: 40px;
  outline: none;
  font-family: inherit;
  font-size: 18px;
  background-color: transparent;
  border: none;
  color: white;
  border-radius: 5px;
  padding: 5px;
  box-shadow: 2px 2px 8px 0px rgba(255, 255, 255, 0.2);
  margin-right: 10px;
}

.input:focus {

}

.errorInput {
  box-shadow: 2px 2px 8px 0px rgba(204, 0, 0, 0.2);
}

.button {
  user-select: none;
  width: 40px;
  height: 40px;
  border: none;
  outline: none;
  font-size: 16px;
  color: white;
  background-color: transparent;
  font-family: inherit;
  cursor: pointer;
  transition: 0.1s ease-in;
}

.button:focus {
  background-color: rgba(255, 255, 255, 0.2);
}

.button:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.button:active {
  transform: scale(105%);
}

.button:disabled:hover {
  cursor: default;
  background-color: transparent;
}

.button:disabled {
  color: transparent;
}

.text {
  font-size: 20px;
  color: rgba(255, 255, 255, 0.6);
}

.greeting {
  max-width: 300px;
  max-height: 300px;
  overflow-y: hidden;
  word-wrap: break-word;
  font-weight: 500;
  font-size: 14px;
}
