.message {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  margin-bottom: 20px;
}

.imageAndText {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
}

.imageAndText img {
  user-select: none;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-left: 15px;
}

.text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.2);
  position: relative;
  max-width: 700px;
}

.text:after {
  content: " ";
  position: absolute;
  right: -10px;
  top: 20px;
  border-top: 10px solid transparent;
  border-right: none;
  border-left: 10px solid rgba(255, 255, 255, 0.2);
  border-bottom: 10px solid transparent;
}

.name {
  align-self: flex-end;
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 5px;
}

.messageText {
  line-height: 22px;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-size: 16px;
}

.time {
  user-select: none;
  position: absolute;
  bottom: -15px;
  right: 7px;
  font-size: 12px;
  font-weight: 500;
}

