.sendForm {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  outline: none;
}

.textarea {
  width: 100%;
  border: none;
  outline: none;
  padding: 10px;
  font-family: inherit;
  font-size: 18px;
  border-radius: 10px;
  background-color: transparent;
  color: white;
  font-weight: 500;
  box-shadow: 3px 5px 22px -7px rgba(255, 255, 255, 0.2);
  line-height: 24px;
}

.textarea::placeholder {
  color: rgba(255, 255, 255, 0.8);
  user-select: none;
  font-size: 16px;
}

.button {
  margin-top: 10px;
  border-radius: 10px;
  outline: none;
  border: none;
  background-color: rgba(255, 255, 255, 0.2);
  color: white;
  font-family: inherit;
  font-size: 16px;
  cursor: pointer;
  width: 70px;
  height: 30px;
  user-select: none;
  align-self: flex-end;
  transition: 0.1s ease-in;
}

.button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}
.button:active {
  transform: scale(105%);
}
