.friendMessage {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 20px;
}

.friendImageAndText {
  display: flex;
  align-items: center;
}

.friendImageAndText img {
  user-select: none;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-right: 15px;
}

.friendText {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.2);
  position: relative;
  max-width: 700px;
}

.friendText:after {
  content: " ";
  position: absolute;
  left: -10px;
  top: 20px;
  border-top: 10px solid transparent;
  border-right: 10px solid rgba(255, 255, 255, 0.2);
  border-left: none;
  border-bottom: 10px solid transparent;
}

.friendName {
  align-self: flex-start;
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 5px;
}

.friendMessageText {
  line-height: 22px;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-size: 16px;
}

.friendTime {
  user-select: none;
  position: absolute;
  bottom: -15px;
  left: 7px;
  font-size: 12px;
  font-weight: 500;
}
