.button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 40px;
  cursor: pointer;
  color: white;
  font-size: 16px;
  font-family: inherit;
  border-radius: 10px;
  padding: 5px;
  border: none;
  transition: 0.1s ease-in;
}

.disabled {
  opacity: 0.5;
}

.secondary {
  background-color: rgba(184, 109, 255, 0.2);
}

.default {
  background-color: rgba(255, 255, 255, 0.2);
}

.red {
  background-color: rgba(204, 0, 0, 0.2);
}

.default:hover {
  background-color: rgba(255, 255, 255, 0.3);
}

.red:hover {
  background-color: rgba(204, 0, 0, 0.3);
}

.secondary:hover {
  background-color: rgba(184, 109, 255, 0.3);
}

.disabled:hover {
  background-color: rgba(204, 0, 0, 0.2);
}


.default:active {
  transform: scale(105%);
}

.secondary:active {
  transform: scale(105%);
}

.red:active {
  transform: scale(105%);
}

.button:disabled:active {
  transform: scale(100%);
}

.button:disabled {

}
