@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap');

.App {
  --header_height: 60px;

  padding: 107px 70px;

  font-family: 'Montserrat', sans-serif;
  color: white;

  background: linear-gradient(-45deg, #111111, #1c1c1c, #d52367, #d52367);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  min-height: 100vh;
  height: 100%;
}

*::selection {
  background-color: rgba(46, 112, 255, 0.8);
}

.hw {
  display: flex;
  flex-direction: column;
  padding-bottom: 40px;
}

.homework2 {
  align-content: center;
}

.hwTitle {
  user-select: none;
  font-size: 32px;
  margin-bottom: 20px;
}

@keyframes gradient {
  0% {
    background-position: 100% 50%;
  }
  50% {
    background-position: 70% 50%;
  }
  100% {
    background-position: 100% 50%;
  }
}

@media screen and (max-width: 980px) {
  .App {
    padding: 20px;
  }
}