.input:focus {
  outline: none;
  box-shadow: 4px 4px 8px 0 rgba(255, 255, 255, 0.4);
}

.input {
  width: 300px;
  height: 40px;
  background-color: transparent;
  border: none;
  box-shadow: 4px 4px 8px 0 rgba(255, 255, 255, 0.2);
  border-radius: 5px;
  color: white;
  font-family: inherit;
  font-size: 18px;
  padding: 5px;
  transition: 0.1s ease-in;
}

.inputWrapper {
  display: flex;
  flex-direction: column;
}

.superInput {
}

.errorInput {
  box-shadow: 4px 4px 8px 0 rgba(204, 0, 0, 0.4);
}

.errorInput:focus {
  box-shadow: 4px 4px 8px 0 rgba(204, 0, 0, 0.6);
}

.errorInput:focus {
}

.error {
  font-size: 14px;
  color: #c00;
  font-weight: 600;
  margin-top: 5px;
}

